import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class AddchemistService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    AddChemist(ChemistEmail,ChemistName,ChemistPassword,Longitude,latitide,ChemistId)
    {
        let body = JSON.stringify({ Email: ChemistEmail, Password: ChemistPassword,ChemistName: ChemistName,Longitude: Longitude,Latitude: latitide,ChemistId: ChemistId});
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/addChemistDetail", body, options)
            .map(res => res.json());
    }
 
   
}









